<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="6">
          <b-card-group>
            <b-card no-body class="p-4">
              <b-card-body>
                <img src="../assets/images/logo.png" class="user-avatar" />
                <b-form>
                  <h1 class="page-title">Login</h1>
                  <p class="error-msg">{{ errorMsg }}</p>
                  <!--<p class="text-muted">Sign In to your account</p>-->
                  <b-input-group class="mb-3">
                    <b-input-group-prepend><b-input-group-text><i class="icon-user"></i></b-input-group-text></b-input-group-prepend>
                    <b-form-input type="text" class="form-control" placeholder="Username" autocomplete="username email" />
                  </b-input-group>
                  <b-input-group class="mb-4">
                    <b-input-group-prepend><b-input-group-text><i class="icon-lock"></i></b-input-group-text></b-input-group-prepend>
                    <b-form-input type="password" class="form-control" placeholder="Password" autocomplete="current-password" />
                  </b-input-group>
                  <b-row>
                    <b-col cols="6">
                      <b-button variant="primary" class="px-4" @click="authenticate">Login</b-button>
                    </b-col>
                    <b-col cols="6" class="text-right">
                      <b-button router-link to="/forgot-password" variant="link" class="px-0">Lupa Password?</b-button>
                    </b-col>
                  </b-row>
                </b-form>
              </b-card-body>
            </b-card>
          </b-card-group>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
import { EventBus } from '../_auth/utils/eventbus'

export default {
  name: 'Login',
  data () {
    return {
      email: 'superdev',
      password: 'pegelinuxsuperdev',
      errorMsg: ''
    }
  },
  methods: {
    authenticate () {
      this.$store.dispatch('login', { username: this.email, password: this.password })
        .then(() => this.$router.push('/'))
    },
  },
  mounted () {
    EventBus.$on('failedAuthentication', (msg) => {
      this.$router.push('/');
      this.errorMsg = msg
    })
  },
  beforeDestroy () {
    EventBus.$off('failedAuthentication')
  }
}
</script>

